import { bottomOfPage } from '../utils/HandleClick'; 

const handleScroll =  () => {
    let arrow = document.querySelector('.arrow');
    if(bottomOfPage()) {
        if(!arrow.classList.contains('arrow-bottom-of-page')) {
            arrow.classList.add('arrow-bottom-of-page');
        }
    }
    else {
        if(arrow.classList.contains('arrow-bottom-of-page')) {
            arrow.classList.remove('arrow-bottom-of-page');
        }
    }
}

export default handleScroll;