import React, { Component } from 'react';
import Home from '../home/components/main/js/Home';
import Arrow from '../home/components/arrow/js/Arrow';
import Projects from '../projects/main/js/Projects';
import Skills from '../skills/main/js/Skills';
import Footer from '../footer/main/js/Footer';
import handleScroll from '../home/components/arrow/utils/HandleScroll';

export default class AllPages extends Component {

    componentDidMount() {
        document.addEventListener('scroll', (e) => handleScroll(e));
    }

    render() {
        return(
            <div className='all-pages'>
                <Home />
                <Arrow />
                <Projects />
                <Skills />
                <Footer />
            </div>
        );
    }
}