export default function handleClick(e) {
    if(bottomOfPage()) {
        let home = document.querySelector('.home');
        home.scrollIntoView();
    }
    else {
        let nextDiv = moveTo();
        nextDiv.scrollIntoView();
    }

}

function moveTo() {
    let pages = getPages();
    for(let i = 0; i < pages.length; i++) {
        if(pages[i].offsetTop > window.pageYOffset) {
            return pages[i];
        }
    }
}

function getPages() {
    let allpages = document.querySelector('.all-pages');
    return Array.from(allpages.children).filter((div) => {
        return !div.classList.contains('arrow');
    })
}

export function bottomOfPage() {
    return Math.ceil(window.innerHeight) + Math.ceil(document.documentElement.scrollTop) >= document.body.offsetHeight;
}