import React, { Component } from 'react';
import '../css/Projects.css';
import Project from '../../project/Project';
import Kodflix from '../../img/kodflix.png';
import HouseMarketImg from '../../img/house-market.png';
import RestaurantImg from '../../img/restaurant.png';
import ReducePresentationImg from '../../img/reduce-presentation.png';

export default class Projects extends Component {
    render() {
        return(
            <div className='projects'>
                <div className='projects-main-title'>Projects</div>
                <div className='all-projects'>
                    {
                        projects.map((project) => (
                            <Project project={project} />
                        ))
                    }
                </div>
            </div>
        );
    }
}

let projects = [
    {
        title: 'Kodflix',
        image: Kodflix,
        link: 'https://osman-kodflix.herokuapp.com/'
    },
    {
        title: 'House Market',
        image: HouseMarketImg,
        link: 'http://osman-house-market123.herokuapp.com/'
    },
    {
        title: 'Restaurant',
        image: RestaurantImg,
        link: 'https://maruntav.github.io/restaurant3578/'
    },
    {
        title: 'JavaScript Tutorial Video',
        image: ReducePresentationImg,
        link: 'https://www.youtube.com/watch?v=MIkRK5co4kk'
    }
];