import React from 'react';
import ContactImages from '../../utils/ContactImages';
import '../css/Contact.css';

const contact = (props) => (
    <div className='contact'>
        {
            ContactImages.map((contactImage) => (
                <a href={contactImage.link} className='contact-item'>
                    <img src={contactImage.image} alt={contactImage.link} />
                </a>
            ))
        }
    </div>
);

export default contact;